import react, { useEffect, useState } from "react"
import { RESTAURANT_MENU_LIST_URL } from "./constants"
import { useParams } from "react-router"

const UseRestaurantMenuList =  ()=>{
    const [restoMenuList,setRestoMenuList] = useState([])
    const {id} = useParams()
    useEffect(()=>{
        fetchList()
    },[])

    const fetchList= async ()=>{
        const fetchData = await fetch(RESTAURANT_MENU_LIST_URL+id)
        const json = await fetchData.json();
        setRestoMenuList(json?.data)
    }
    return restoMenuList
}

export default UseRestaurantMenuList;