const RestaurantCard = (props) => {
  const { name, cuisines, avgRating, costForTwo, cloudinaryImageId, sla } =
    props?.restoData?.info;
  return (
    <div className="w-56 h-auto bg-white p-4 m-4 rounded-lg items-center hover:scale-90 ease-in-out duration-300  ">
      <img
        className="w-40 mx-4 my-2 rounded-lg "
        src={
          "https://media-assets.swiggy.com/swiggy/image/upload/" +
          cloudinaryImageId
        }
        alt="res-logo"
      />
      <h3 className="font-bold" >{name}</h3>
      <h4 className="text-orange-700 text-sm" > {cuisines.join(", ")} </h4>
      <div className="flex" >
        <div className="rounded-lg" ><span></span></div>
      <h4> {avgRating} </h4>
      </div>
      <h4 className="text-sm" > {costForTwo} </h4>
      <h3 className="text-sm" >{sla.slaString}</h3>
    </div>
  );
};

// Implementing Higher Order Functions
export const DiscountedInfo = (RestaurantCard) => {
  return (props) => {
    const {aggregatedDiscountInfoV3} = props?.restoData?.info
    return (
      <div>
        <label className="absolute p-1 m-6 rounded-lg text-black bg-white text-xl"> {aggregatedDiscountInfoV3.header} {aggregatedDiscountInfoV3.subHeader} </label>
        <RestaurantCard {...props} />
      </div>
    );
  };
};

export default RestaurantCard;
