import RestaurantCard, {DiscountedInfo} from "./RestarantCard";
import { useEffect, useState } from "react";
import Shimmer from "./Shimmer";
import { Link } from "react-router";

const Body = () => {
    const [restaurantList,setRestaurantList] = useState([])
    const [searchText,setsearchText] = useState("")
    const [filteredRestaurantList,setFilteredRestaurantList] = useState([])
    const DiscountedInfoTag = DiscountedInfo(RestaurantCard); 

    
    const filterTopRatedRestaurants = ()=>{
        const topRatedRestaurants = restaurantList?.filter((restaurant) => restaurant?.info?.avgRating >= 4.5)
        setFilteredRestaurantList(topRatedRestaurants)
    }
// whenever the state variable updates, React triggers a re-conciliation cycle[re-renders the component].
// If no dependancy array => useEffect is called on every render.
// If the dependancy array is empty =[] => useEffect is called on only initial render and just once.
// If the dependancy array is restaurantList[A state variable] => useEffect is called only when that state variable[restaurantList] is updated. 
    useEffect(()=>{
        fetchData();
    },[])
    // akshai-code https://www.swiggy.com/dapi/restaurants/list/v5?lat=12.9351929&lng=77.62448069999999&page_type=DESKTOP_WEB_LISTING

    const fetchData = async ()=>{
        const fetchApiData = await fetch("https://www.swiggy.com/dapi/restaurants/list/v5?lat=12.9781291&lng=77.5695295&is-seo-homepage-enabled=true&page_type=DESKTOP_WEB_LISTING")
        const json = await fetchApiData.json();
        //optional chaining
        setRestaurantList(json?.data?.cards[1]?.card?.card?.gridElements?.infoWithStyle?.restaurants)
        setFilteredRestaurantList(json?.data?.cards[1]?.card?.card?.gridElements?.infoWithStyle?.restaurants)
        
    }
    return (
      <div className="body">
        <div className="search-rate">
          <div>
            <input className="p-2 m-2 w-8/12 rounded-lg " placeholder="Search by restaurant" value={searchText} onChange={(e)=>setsearchText(e.target.value)}/>
            <button className="bg-green-700 p-2 rounded-lg m-2 text-white"  onClick={()=>{
                const filteredArray = restaurantList?.filter((res)=>
                res?.info?.name.toLowerCase().includes(searchText.toLowerCase())
                );
                setFilteredRestaurantList(filteredArray)
            }} >Search</button>
          </div>
          <button className="bg-green-700 p-2 rounded-lg m-2 text-white" onClick={()=> {filterTopRatedRestaurants()} } >Top Rated Restaurants</button>
        </div>
        {restaurantList?.length === 0 ? <Shimmer/> : <div className="resto-container">
          {filteredRestaurantList?.map((restaurant) =>(
            <Link key={restaurant?.info?.id} to={"/restaurant/"+restaurant?.info?.id} >
              { restaurant?.info?.aggregatedDiscountInfoV3 ? <DiscountedInfoTag restoData={restaurant} /> : <RestaurantCard restoData={restaurant} /> }
            </Link>
          ))}
        </div>}
      </div>
    );
  };

  export default Body;