import React from "react";
import ReactDOM from "react-dom/client";
import Header from "./Components/Header";
import Body from "./Components/Body";
import { createBrowserRouter,Outlet,RouterProvider } from "react-router";
import About from "./Components/About";
import ContactUs from "./Components/Contactus";
import ErrorElement from "./Error";
import RestaurantMenu from "./Components/Restaurant Details/RestaurantMenu";
import RestoMenu from "./Components/Restaurant Details/Restaurant Menu/RestoMenu";


// Here we have done a component composition ,i.e a component is inside other component.

// RouterProvider will actually provide this routing configuration to  our app.

const AppLayout = () => {
  return (
    <div className="app">
      <Header />
      <Outlet />
    </div>
  );
};

//routing configuration
const AppRouter = createBrowserRouter([
  {
    path:"/",
    element: <AppLayout/>,
    children:[
      {
        path:"/",
        element: <Body/>
      },
      {
        path:"/about",
        element: <About/>
      },
      {
        path:"/contactUs",
        element: <ContactUs/>
      },
      {
        path:"/restaurant/:id",
        element:<RestoMenu/>
      }
    ],
    errorElement: <ErrorElement />
  },
])

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<RouterProvider router={AppRouter} />);
